.App {
  text-align: center;
  font-family: Arial, sans-serif;
  color: #333;
  background-color: #f4f4f4;
}

.App-header {
  background-color: #333;
  padding: 20px;
  color: white;
}

main {
  padding: 20px;
}

section {
  margin: 20px;
  padding: 20px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h2 {
  color: #333;
  margin-bottom: 20px;
}

button {
  background-color: #333;
  color: white;
  border: none;
  padding: 10px 20px;
  margin-top: 20px;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #555;
}

.input-group {
  margin-bottom: 20px;
}

.item {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 20px;
}

.playerCharacter {
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  margin-bottom: 20px;
}

ul {
  list-style: none;
  padding: 0;
}